  <div class="post-wrapper">
    <article class="post reveal">
      <section class="meta">
        <div class="meta" id="header-meta">
          <h2 class="title">
            <a :href="article.html_url" target="_blank">{{filterTitle}}</a>
          </h2>
          <div class="new-meta-box">
            <div class="new-meta-item author">
              <a href="https://xaoxuu.com" rel="nofollow">
                <img
                  src="data:image/png;base64,"
                  data-original="https://cdn.jsdelivr.net/gh/xaoxuu/assets@master/avatar/avatar.png"
                />
                <p>{{commit.author.name}}</p>
              </a>
            </div>
            <div class="new-meta-item date">
              <a class="notlink">
                <i class="fas fa-calendar-alt" aria-hidden="true"></i>
                <p>{{commit.author.date|dateFormat}}</p>
              </a>
            </div>
            <div class="new-meta-item category">
              <a href="/blog/categories/dev/shell-scripts/" rel="nofollow">
                <i class="fas fa-folder-open" aria-hidden="true"></i>
                <p>Dev&nbsp;/&nbsp;Shell Scripts</p>
              </a>
            </div>
          </div>
          <hr />
        </div>
      </section>
      <section class="article typo">
        <div class="article-entry" itemprop="articleBody">
          <p>
            <a
              href="https://img.vim-cn.com"
              rel="external nofollow noopener noreferrer"
              target="_blank"
            >img.vim-cn.com</a>是一个十分干净好用的图床，本站以及博客中的所有图片都是使用的这个图床。
          </p>
          <p>
            所以，这是一个快速批量
            <strong>上传图片</strong>的脚本。
          </p>
          <div>
            <img
              src="data:image/png;base64,"
              data-original="https://img.vim-cn.com/0d/e463788b9eb82f82b551d8da1cd4cbae9d64cb.png"
              alt
            />
            <div></div>
          </div>
          <div class="readmore">
            <a href="/projects/vim-cn.sh/" class="flat-box">
              <i class="fas fa-book-open fa-fw" aria-hidden="true"></i> 阅读全文
            </a>
          </div>
        </div>

        <div class="full-width auto-padding tags">
          <a href="/blog/tags/图床/" rel="nofollow">
            <i class="fas fa-hashtag fa-fw"></i>图床
          </a>
        </div>
      </section>
    </article>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  name: "Demo",
  props: {
    article: Object
  },
  created() {
    this.$http("/articles/detail", {
      params: {
        path: this.article.path,
        name: this.article.name
      }
    })
      .then(res => {
        if (res.isSuccess) {
          let records = res.data;
          this.commit = records[records.length - 1].commit;
        }
      })
      .catch(error => {
        console.log(error);
      });
  },
  data() {
    return {
      commit: {
        author: {
          name: "ShoneSingLone",
          email: "shonesinglone@gmail.com",
          date: Date.now()
        }
      }
    };
  },
  computed: {
    filterTitle() {
      let title = this.article.name;
      let matches = title.match(/(.*).md$/);
      return matches.length === 2 ? matches[1] : title;
    }
  },
  filters: {
    dateFormat: _date => dayjs(_date).format("YYYY-MM-DD")
  },
  methods: {},
  mounted() {}
};
</script>
